import React from 'react';
import styles from './InputWithText.module.css';
import { Typography, Row, Col, Input } from 'antd';

const InputWithText = ({ disabled = false, text, value = '', handleChange = null }) => {
	return (
		<div className={styles.wrapInput}>
			<Typography.Text className={styles.textInput}>{text}</Typography.Text>
			<Input size="small" value={value} disabled={disabled} className={styles.input} onChange={handleChange} />
		</div>
	);
};

export default InputWithText;
