function createTolerance(...spacings) {
	const nominalSizes = [
		{ min: 0, max: 3 },
		{ min: 3, max: 6 },
		{ min: 6, max: 10 },
		{ min: 10, max: 18 },
		{ min: 18, max: 30 },
		{ min: 30, max: 50 },
		{ min: 50, max: 80 },
		{ min: 80, max: 120 },
		{ min: 120, max: 180 },
	];

	let toleranceArray = [];

	for (let i = 0; i < nominalSizes.length; i++) {
		const nominalSize = nominalSizes[i];
		const spacing = spacings[i];

		if (spacing === undefined) break;
		toleranceArray.push({ ...nominalSize, upperDeviation: spacing[0], bottomDeviation: spacing[1] });
	}

	return toleranceArray;
}

