import React from 'react';
import './TableSquare.css';
import { Table } from 'antd';

const TableSquare = (props) => {
	const { columns, data } = props;
	return (
		<Table
			columns={columns}
			dataSource={data}
			rowClassName={'row-square'}
			className={'table-square'}
			bordered
			pagination={false}
			size="middle"
			// onRow={(record) => ({
			// 	onClick: () => console.log(record),
			// })}
		/>
	);
};

export default TableSquare;
