import React from 'react';
import styles from './ToleranceTable.module.css';
import { Typography, Row, Col, Radio, Divider, Button } from 'antd';

import TableSquare from 'components/UI/TableSquare';
import InputWithText from 'components/UI/InputWithText';
import { withStore } from 'lib/hoc';
import { useStores } from 'lib/hooks';

function ToleranceTable() {
	const { toleranceStore } = useStores();
	const {
		currentValue: { formType, toleranceClass, size, upperDeviation, bottomDeviation },
	} = toleranceStore;

	function handlerClick() {
		toleranceStore.getTolerance();
	}

	return (
		<div className="wrapper-content">
			<Typography.Title level={3}>Таблица допусков</Typography.Title>

			<Row gutter={[16, 16]}>
				<Col>
					<Radio.Group
						defaultValue={formType}
						onChange={(e) => toleranceStore.setFormType(e.target.value)}
						buttonStyle="solid"
					>
						<Radio.Button className={styles.radioButton} value="shaft">
							Вал
						</Radio.Button>
						<Radio.Button className={styles.radioButton} value="hole">
							Отверстие
						</Radio.Button>
					</Radio.Group>
				</Col>
			</Row>

			<Row gutter={[16, 16]}>
				<Col xs={24} sm={12}>
					<InputWithText
						text="Номинальный размер:"
						value={size}
						handleChange={(e) => toleranceStore.setSize(e.target.value)}
					/>
					<InputWithText text="Установленный класс:" value={toleranceClass} disabled={true} />
					<InputWithText text="Верхнее отклонение:" value={upperDeviation} disabled={true} />
					<InputWithText text="Нижнее отклонение:" value={bottomDeviation} disabled={true} />
				</Col>
				<Col xs={24} sm={12}>
					<TableSquare columns={toleranceStore.getColumns} data={toleranceStore.getData} />
				</Col>
			</Row>

			<Divider />

			<Row>
				<Col>
					<Button type="primary" onClick={handlerClick}>
						Применить
					</Button>
				</Col>
			</Row>
		</div>
	);
}

export default withStore(ToleranceTable);
