import { decorate, observable, action, computed } from 'mobx';
import { toleranceData } from 'data/index';

export class ToleranceStore {
	//#region Observable
	dataTolerance = toleranceData();

	currentValue = {
		formType: 'shaft',
		toleranceClass: '',
		size: 0,
		upperDeviation: 0,
		bottomDeviation: 0,
	};
	//#endregion

	/* активная ячейка в таблице */
	currentActiveCell = null;

	/* массив колонок таблицы */
	columns = [
		{
			title: '',
			dataIndex: 'quality',
			key: 'quality',
		},
		{
			title: 'f',
			dataIndex: 'f',
			key: 'f',
			onCell: () => ({
				onClick: (e) => this.onClickCell(e.target),
			}),
		},
		{
			title: 'g',
			dataIndex: 'g',
			key: 'g',
			onCell: () => ({
				onClick: (e) => this.onClickCell(e.target),
			}),
		},
		{
			title: 'h',
			dataIndex: 'h',
			key: 'h',
			onCell: () => ({
				onClick: (e) => this.onClickCell(e.target),
			}),
		},
		{
			title: 'k',
			dataIndex: 'k',
			key: 'k',
			onCell: () => ({
				onClick: (e) => this.onClickCell(e.target),
			}),
		},
	];

	/* массив данных таблицы */
	data = [
		{ key: 1, quality: '8', f: 'f8', g: 'g8', h: 'h8', k: 'k8' },
		{ key: 2, quality: '9', f: 'f9', g: 'g9', h: 'h9', k: 'k9' },
		{ key: 3, quality: '10', f: 'f10', g: 'g10', h: 'h10', k: 'k10' },
		{ key: 4, quality: '11', f: 'f11', g: 'g11', h: 'h11', k: 'k11' },
	];

	//#region Геттеры данных, в зависимости от параметра currentValue.formType
	get getColumns() {
		if (this.currentValue.formType === 'hole') {
			const res = this.columns.map((el, i) => {
				if (i === 0) {
					return { ...el };
				}
				return {
					...el,
					title: el.title.toUpperCase(),
					key: el.key.toUpperCase(),
				};
			});
			return res;
		}
		return this.columns;
	}

	get getData() {
		if (this.currentValue.formType === 'hole') {
			const res = this.data.map((el) => {
				return {
					...el,
					f: el.f.toUpperCase(),
					g: el.g.toUpperCase(),
					h: el.h.toUpperCase(),
					k: el.k.toUpperCase(),
				};
			});
			return res;
		}
		return this.data;
	}
	//#endregion

	/* функция активной ячейки */
	onClickCell(cell) {
		if (this.currentActiveCell) {
			this.currentActiveCell.classList.remove('table-select');
		}
		cell.classList.add('table-select');

		this.currentActiveCell = cell;
		this.currentValue.toleranceClass = cell.textContent;
	}

	setFormType(val) {
		this.currentValue.formType = val;
	}

	setSize(val) {
		this.currentValue.size = val.trim();
	}

	outputError(err) {
		switch (err) {
			case 10:
				console.log('Введите корректное число в диапазоне от 0 до 120');
				break;

			default:
				console.log('Неизвестная ошибка');
				break;
		}
	}

	getTolerance() {
		const qualityClass = this.currentValue.toleranceClass;

		const idx = this.nominalArr.findIndex((nominal) => nominal > this.currentValue.size);
		if (idx === -1) {
			return this.outputError(10);
		}

		if (this.dataTolerance.hasOwnProperty(qualityClass)) {
			const qualityObj = this.dataTolerance[qualityClass];
			this.currentValue.upperDeviation = qualityObj[idx - 1].upperDeviation;
			this.currentValue.bottomDeviation = qualityObj[idx - 1].bottomDeviation;
		}
	}

	nominalArr = [0, 3, 6, 10, 18, 30, 50, 80, 120, 180];
}

decorate(ToleranceStore, {
	dataTolerance: observable,
	currentValue: observable,
	setFormType: action,
	setSize: action,
	getColumns: computed,
	getData: computed,
});
